import { c as connection } from "../../../chunks/connection.js";
import { C as ContestantSchema } from "../../../chunks/types.js";
import { r as redirect } from "../../../chunks/index.js";
const load = ({ locals }) => {
  if (locals.user) {
    throw redirect(302, "/dashboard");
  }
};
const actions = {
  default: async ({ request }) => {
    const data = await request.formData();
    const name = data.get("name").trim();
    const email = data.get("email").trim();
    const phone = data.get("phone").trim();
    const password = data.get("password").trim();
    const college = data.get("college").trim();
    const parsed = ContestantSchema.safeParse({
      name,
      email,
      password,
      phone,
      college
    });
    if (parsed.success) {
      try {
        const existing = await connection.findParticipantByAttribute({ email });
        if (existing) {
          return {
            success: false,
            message: "User already exist"
          };
        }
        const created = await connection.createParticipant(parsed.data);
      } catch (e) {
        return {
          success: false,
          error: "Failed to create participant"
        };
      }
      throw redirect(302, "/login");
    }
    return {
      success: false,
      error: parsed.error.format(),
      data: {
        name,
        email,
        password,
        phone
      }
    };
  }
};
export {
  actions,
  load
};
